import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  MenuItem,
  Box,
  Alert,
} from '@mui/material';

interface AddRobotModalProps {
  open: boolean;
  onClose: () => void;
  onAdd: (robot: { name: string; robotType: string; homebase: string }) => Promise<void>;
  token: string | null;
}

export const AddRobotModal: React.FC<AddRobotModalProps> = ({
  open,
  onClose,
  onAdd,
  token,
}) => {
  const [name, setName] = useState('');
  const [robotType, setRobotType] = useState('humanoid');
  const [homebase, setHomebase] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!name || !robotType || !homebase) {
      setError('Bitte fllen Sie alle Felder aus');
      return;
    }

    try {
      await onAdd({ name, robotType, homebase });
      setName('');
      setRobotType('humanoid');
      setHomebase('');
      onClose();
    } catch (err) {
      if (err instanceof Error) {
        setError(err.message);
      } else {
        setError('Fehler beim Erstellen des Roboters');
      }
    }
  };

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Neuen Roboter hinzufgen</DialogTitle>
      <form onSubmit={handleSubmit}>
        <DialogContent>
          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              label="Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              fullWidth
              required
            />
            <TextField
              select
              label="Typ"
              value={robotType}
              onChange={(e) => setRobotType(e.target.value)}
              fullWidth
              required
            >
              <MenuItem value="humanoid">Humanoid</MenuItem>
              <MenuItem value="dog">Roboterhund</MenuItem>
            </TextField>
            <TextField
              label="Homebase"
              value={homebase}
              onChange={(e) => setHomebase(e.target.value)}
              fullWidth
              required
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose}>Abbrechen</Button>
          <Button type="submit" variant="contained" color="primary">
            Hinzufgen
          </Button>
        </DialogActions>
      </form>
    </Dialog>
  );
};
